var loginRes,_self,page=1;
export default {
	data() {
		return {
			myFace : '',
			arts : [],
			loadMore : "点击加载更多",
			user : []
		};
	},
	methods: {
		logoff : function(){
			uni.removeStorageSync('SUID');
			uni.removeStorageSync('SRAND');
			uni.showToast({
				title:"您已经退出悦读",
				icon: "none"
			});
			setTimeout(function(){
				uni.switchTab({
					url:'../index/index'
				});
			}, 1000);
		},
		getArtList : function(){
			if(this.loadMore != '点击加载更多'){return ;}
			this.loadMore = '加载中...';
			uni.request({
				url: this.apiServer + this.artList,
				method: 'GET',
				header: {'content-type' : "application/x-www-form-urlencoded"},
				data: {
					uid 	: loginRes[0],
					pageNo	: page,
					pageSize: 3
					
				},
				success: res => {
					console.log(page + '::' + JSON.stringify(res.data.result.records));
					var pageSize = res.data.result.pages;
					if(res.data.result.records.length != 0 ){
						this.arts = this.arts.concat(res.data.result.records);
						page++;
						if(page <= pageSize){
							this.loadMore = '点击加载更多';
						}else{
							this.loadMore = '已经加载全部';
						}
					}else{
						this.loadMore = '已经加载全部';
					}
				}
			});
		},
		editArt : function(e){
			var artId = e.currentTarget.dataset.artid;
			uni.navigateTo({
				url:"../editArt/editArt?artId="+artId
			});
		},
		removeArt : function(e){
			var artId = e.currentTarget.dataset.artid;
			var index = e.currentTarget.dataset.index;
			uni.showModal({
				title:"提示",
				content:"确定要删除吗?",
				success:function(e){
					if(e.confirm == true){
						uni.request({
							url: _self.apiServer + _self.removeArticle,
							method: 'POST',
							header: {'content-type' : "application/x-www-form-urlencoded"},
							data: {
								uid : loginRes[0],
								artId : artId,
								sign : loginRes[1]
							},
							success: res => {
								console.log(JSON.stringify(res));
								if(res.data.success){
									uni.showToast({title: "已删除", icon:"none"});
									_self.arts.splice(index, 1);
								}else{
									uni.showToast({title: "删除失败", icon:"none"});
								}
							}
						});
					}
				}
			});
		}								
	},
	onLoad:function(){
		_self = this;
		loginRes = this.checkLogin('../my/my', '2');
		if(!loginRes){return false;}
		_self.myFace = loginRes[2];
	},
	onShow:function(){
		//加载我的文章
		this.arts = [];
		page = 1;
		this.getArtList();	
		// 加载会员信息
		uni.request({
			url: _self.apiServer + _self.info,
			method: 'POST',
			header: {'content-type' : "application/x-www-form-urlencoded"},
			data: {
				uid : loginRes[0],
				random : loginRes[1]
			},
			success: res => {
				console.log(JSON.stringify(res));
				if(res.data.success){
					this.user = res.data.result;
				}
			}
		});
		this.arts = [];
		page = 1;
		this.loadMore = '点击加载更多';
		this.getArtList();
	}
}
